.equalContainer {
    height: 72px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.equalButton {
    width: 232px;
    height: 64px;
    background: #5D5FEF;
    border-radius: 6px;
    border: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 15px;
    color: #FFFFFF;
}

.equalButton:active {
    border: 2px solid #FFFFFF;
}

.inactiveEqualButton {
    pointer-events: none;
}